.menu__burger-icon {
  position: absolute;
  top: 50%;
  right: 15px;
  z-index: 2;
  display: none;
  width: 50px;
  height: 30px;
  transform: translate(0, -50%);
}

.menu__burger-icon span {
  position: absolute;
  right: 0;
  display: block;
  width: 100%;
  height: 4px;
  border-radius: 2px;
  background-color: #c04ae2;
  transition: all 0.3s ease 0s;
}

.menu__burger-icon span:first-child {
  top: 0;
}

.menu__burger-icon span:nth-child(2) {
  top: calc(50% - 2px);
  width: 80%;
}

.menu__burger-icon span:last-child {
  bottom: 0;
  width: 50%;
}

.menu__burger-icon._active span {
  bottom: auto;
  top: calc(50% - 2px);
  width: 100%;
}

.menu__burger-icon._active span:first-child {
  transform: rotate(-45deg);
}

.menu__burger-icon._active span:nth-child(2) {
  display: none;
}

.menu__burger-icon._active span:last-child {
  transform: rotate(45deg);
}

.menu__item {
